main {
    display: flex;
    flex-direction: column;
    align-items: space-evenly;
    gap: 30px;
    margin-bottom: 20px;
}

.container1 {
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-evenly;
    align-items: center;
    background: #eeece8;
    border-radius: 10px;
    outline: 1px solid var(--container-outline-color);
}

.container2 {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    background: #eeece8;
    border-radius: 10px;
    outline: 1px solid var(--container-outline-color);
}

.container3 {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    background: #eeece8;
    border-radius: 10px;
    outline: 1px solid var(--container-outline-color);
    min-height: 200px;
}

#story-info,
#mission-info {
    padding: 20px;
}

#story-img,
#mission-img {
    padding: 20px;
}

img {
    outline: 2px solid var(--container-outline-color);
    padding: 10px;
}

/** Typography **/

h1 {
    font-size: 60px;
    color: var(--h1-color);
}

#page-title {
    text-align: center;
    color: black;
}

h2 {
    font-weight: 100;
}

h3,
h4 {
    font-weight: lighter;
    color: var(--header-footer-background-color);
}

/** Larger resolutions **/
@media only screen and (min-width: 768px) {

    .container1,
    .container2 {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
    }

    #story-info,
    #mission-info {
        padding: 20px;
    }

    #story-img,
    #mission-img {
        padding: 40px;
    }
}