#page-title {
    text-align: center;
}

.big-container {
    margin-bottom: 20px;
    padding: 5px;
    display: flex;
    flex-direction: column;
    gap: 2px;
}

.main-container {
    display: flex;
    flex-direction: column;
    gap: 2px;
}

.container1 {
    display: flex;
    flex-direction: column;
    padding: 10px;
    outline: 1px solid var(--container-outline-color);
    border-radius: 10px;
}

.container2 {
    display: flex;
    flex-direction: column;
    outline: 1px solid var(--container-outline-color);
    padding: 10px;
    border-radius: 10px;
}

.container3 {
    display: flex;
    flex-direction: column;
    padding: 10px;
    outline: 1px solid var(--header-footer-background-color);
    border-radius: 10px;
    text-align: center;
    background-color: var(--container-outline-color);
}

img {
    box-shadow: 5px 5px 5px gray; 
}

/** Typography **/

h1 {
    font-size: 60px;
    color: var(--h1-color);
}

h2 {
    font-weight: 100;
    color: black;
}

h3,
h4 {
    font-weight: lighter;
}

#quote h3 {
    text-align: center;
    color: var(--header-footer-background-color);
}

a {
    text-decoration: none;
    color: black;
}

/** Larger resolutions **/
@media only screen and (min-width: 768px) {

    .big-container {
        display: flex;
        flex-direction: row;
        gap: 20px;
    }

    .main-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;
        width: 100%;
        min-height: calc(100vh - 200px); /* needed to fill the screen the normal amount */
    }

    .container1 {
        display: flex;
        flex: 2;
        flex-direction: column;
        align-items: space-evenly;
        min-width: 970px;
    }

    .container2 {
        display: flex;
        flex: 2;
        flex-direction: column;
        justify-content: space-evenly;
        min-width: 970px;
    }

    .container3 {
        display: flex;
        flex: 3;
        flex-direction: column;
        justify-content: space-evenly;
        min-width: 200px;
    }
}